<script>
import PatientService from '@services/patient'

export default {
  props: {
    visit: {
      type: Object,
      required: true,
    },
  },

  data () {
    return {
      fields: [
        {
          name: 'created_at',
          title: 'Date',
          titleClass: 'text-right',
          dataClass: 'text-right',
          callback: this.$options.filters.DATE_MONTH_YEAR_HOUR_MINUTES,
        },
        {
          name: 'note',
          title: 'Note',
        },
        {
          name: 'user.name',
          title: 'Written By',
        },
        {
          name: '__slot:actions',
          dataClass: 'text-right ',
        },
      ],

      moreParams: {
        filter: { patient_id: this.visit.patient_id },
      },

      form: new window.Form({
        id: '',
        patient_visit_id: this.visit.id,
        status: 0,
        note: '',
      }),

      status: [
        { name: 'Shared', value: 0 },
        { name: 'Private', value: 1 },
      ],

      selectedMedicalNote: null,

      patientVisitRecords: this.patientVisits,
    }
  },

  computed: {
    vuetable () {
      return this.$refs.table.$refs.vuetable
    },
  },

  watch: {
    'moreParams.filter.patient_visit_id': function (newId) {
      if (newId) {
        this.vuetable.refresh()
      }
    },
  },

  methods: {
    showCreateForm () {
      this.$bvModal.show('medical-modal')
    },

    viewMedicalNote (note) {
      this.selectedMedicalNote = note

      this.$bvModal.show('view-medical-modal')
    },

    createMedical () {
      this.form.post('/medicals')
        .then(() => {
          this.$bvModal.hide('medical-modal')

          this.$toast.success('Medical Note Saved')
        })
        .catch()
    },

    showUpdateForm (note) {
      this.form.populate(note)

      this.$bvModal.show('medical-update-modal')
    },

    updateMedical () {
      this.form.patch('/medicals/' + this.form.id)
        .then(() => {
          this.$bvModal.hide('medical-update-modal')
          this.$toast.success('Changes Saved')
        })
        .catch()
    },

    deleteMedical (rowData) {
      this.$toast
        .question('Are you sure', 'You are deleting Medical Record')
        .then(() => {
          PatientService.deleteMedical(rowData.id)
            .then(() => {
              this.vuetable.reload()
              this.$toast.info('Medical record deleted')
            })
            .catch(() => {
              this.$toast.error('The medical note could not be deleted')
            })
        })
    },

    cleanUp () {
      this.vuetable.reload()
    },

    findAllPatientVisits () {
      PatientService.findAllPatientsVisits({
        page: { size: 10000 },
        filter: { patient_id: this.patientId },
        sort: '-date',
      })
        .then(({ data }) => {
          this.patientVisits = data.data
        })
        .catch((error) => {
          console.log(error)
          this.$toast.error('Failed loading patient visits')
        })
    },
  },
}
</script>

<template>
  <div>
    <div class="card">
      <div class="card-header">
        <h2>Notes</h2>

        <div class="card-actions">
          <a href="#" class="btn" @click.prevent="showCreateForm">
            <i class="uil uil-plus me-1" /> Write Note
          </a>
        </div>
      </div>
      <VueTable
        ref="table"
        api-url="/medicals"
        :fields="fields"
        :append-params="moreParams"
      >
        <template #date="props">
          <div class="media-body align-self-center">
            <span class="text-sm">{{ props.rowData.created_at | date_DATE_MONTH_YEAR }} </span>
            <div class="font-size-12 text-purple-400">
              <div id="tooltip-target-1" class="text-truncate d-flex justify-content-end">
                <div>{{ props.rowData.created_at | TIME_HOUR_MINUTES }}</div>
              </div>
            </div>
          </div>
        </template>

        <template #actions="props">
          <div class="dropdown">
            <button
              class="btn dropdown-toggle align-text-top"
              data-bs-toggle="dropdown"
            >
              Menu
            </button>
            <div class="dropdown-menu dropdown-menu-end">
              <a class="dropdown-item" href="#" @click="viewMedicalNote(props.rowData)">
                <i class="uil uil-eye me-2" /> View
              </a>
              <div class="dropdown-divider" />

              <a class="dropdown-item" href="#" @click="showUpdateForm(props.rowData)">
                <i class="uil uil-edit me-2" /> Update
              </a>

              <!--                            <a class="dropdown-item text-danger" href="#" @click="deleteMedical(props.rowData)">-->
              <!--                                <i class="uil uil-trash-alt me-2"></i> Delete-->
              <!--                            </a>-->
            </div>
          </div>
        </template>
      </VueTable>
    </div>

    <b-modal
      id="medical-modal"
      title="Write Note"
      title-class="font-18"
      no-close-on-backdrop
      size="lg"
      modal-class="modal-blur"
      hide-header-close
      footer-class="bg-light-alpha border-top d-flex justify-content-end"
      lazy
      @hidden="cleanUp"
    >
      <div class="mb-3">
        <textarea
          id="note"
          v-model="form.note"
          type="text"
          placeholder="The medical notes go here ..."
          rows="6"
          class="form-control"
          name="note"
        />

        <span
          v-if="form.errors.has('note')"
          class="text-danger"
          v-text="form.errors.first('note')"
        />
      </div>

      <template #modal-footer="{cancel}">
        <button type="button" class="btn me-auto" :disabled="form.processing" @click.prevent="cancel">
          Dismiss
        </button>
        <button
          type="submit" class="btn btn-primary" :disabled="form.processing"
          @click="createMedical"
        >
          Submit
        </button>
      </template>
    </b-modal>

    <b-modal
      id="medical-update-modal"
      title="Medical Note"
      title-class="font-18"
      no-close-on-backdrop
      hide-header-close
      size="lg"
      footer-class="d-flex justify-content-start"
      lazy
      @hidden="cleanUp"
    >
      <form
        id="updateForm"
        @submit.prevent="updateMedical"
        @keydown="(event) => form.errors.clear(event.target.name)"
      >
        <div class="mb-3">
          <textarea
            id="note"
            v-model="form.note"
            type="text"
            placeholder="The medical notes go here ..."
            rows="6"
            class="form-control"
            name="note"
          />

          <span
            v-if="form.errors.has('note')"
            class="text-danger"
            v-text="form.errors.get('note')"
          />
        </div>

        <!--                <div class="form-group">-->
        <!--                    <label for="status">Visibility-->
        <!--                        <i class="uil uil-question-circle text-primary" id="visiblity_id_helpTex"></i>-->
        <!--                    </label>-->

        <!--                    <b-tooltip target="visiblity_id_helpTex" variant="secondary" placement="right">-->
        <!--                        Private notes will be visible only to you. <br>-->
        <!--                        Shared notes will be visible to all doctors in your department.-->
        <!--                    </b-tooltip>-->

        <!--                    <b-form-radio-group-->
        <!--                        id="status"-->
        <!--                        v-model="form.status"-->
        <!--                        :options="status"-->
        <!--                        name="status"-->
        <!--                        value-field="value"-->
        <!--                        text-field="name"-->
        <!--                        size="lg"-->
        <!--                    ></b-form-radio-group>-->
        <!--                    <span-->
        <!--                        v-if="form.errors.has('status')"-->
        <!--                        class="text-danger"-->
        <!--                        v-text="form.errors.get('status')"-->
        <!--                    />-->
        <!--                </div>-->
      </form>

      <template #modal-footer="{cancel}">
        <button
          form="updateForm"
          type="submit"
          class="btn btn-success"
          :disabled="form.processing"
        >
          {{ form.processing ? 'Processing...' : 'Save Changes' }}
        </button>

        <button
          class="btn btn-light mx-2"
          :disabled="form.processing"
          @click="cancel"
        >
          Close
        </button>
      </template>
    </b-modal>

    <b-modal
      id="view-medical-modal"
      title="Notes Details"
      title-class="font-18"
      no-close-on-backdrop
      size="lg"
      modal-class="modal-blur"
      hide-header-close
      footer-class="bg-light-alpha border-top d-flex justify-content-end"
      lazy
    >
      <div v-if="selectedMedicalNote" class="row">
        <div style="white-space: pre-wrap" v-html="selectedMedicalNote.note" />
      </div>

      <template #modal-footer="{cancel}">
        <a href="#" class="btn" @click="cancel">Dismiss</a>
      </template>
    </b-modal>
  </div>
</template>
